import React from 'react'
import { clearScrollMagic } from './helpers/scrollMagic';
import { Transaction } from './partials/Transaction';

const articleStyle = {
    margin: '0 auto',
    padding: '0 0.5rem',
    maxWidth: '960px',
}

export const Deposit = ({ctx, user}) => {

    const clearHomeActive = () => {
        const home = document.getElementById('homeNavLink');
        home.classList.remove('active');
    }
    setTimeout(clearHomeActive, 50);

    clearScrollMagic();

    return (
        <section style={articleStyle}>
            <Transaction 
                ctx={ctx}
                user={user}
                type='Deposit'
            />

        </section>
    )
}