import { useState, useEffect } from 'react'
import axios from 'axios';
import { newTime } from './helpers/dateTime';
import '../styles/exchange.css';

const Rates = ({data}) => {
    const rates = Object.keys(data).map(function(key, i) {
        return (
            <p key={i}>
                <span className='fw-bolder text-white textShadow dropShadow'>{data[key].name}</span>
                <br/>
                <span className='fw-bolder'>{data[key].unit} </span>
                <span className='cornflowerBlue textShadow'>
                    {data[key].value.toLocaleString(undefined, {'minimumFractionDigits':2,'maximumFractionDigits':2})}
                </span>
            </p>
            )
        }
    )
   
    return (
        <>
        <span>{rates}</span>
        </>
    );
}

const Time = () => {
    const [time, setTime] = useState(newTime);
    
    return <span>{time.toLocaleString()} </span>;
}

const initScroll = () => {
    const exchangeRates = document.getElementById('exchangeRates');
    const exchangeRatesWidth = exchangeRates.scrollWidth;
    
    setInterval(() => {
        const first = document.querySelector('#exchangeRates p');
        
        if (!isElementInViewport(first)) {
            exchangeRates.appendChild(first);
            exchangeRates.scrollTo(exchangeRates.scrollLeft - first.offsetWidth, 0);
        }
    
        if (exchangeRates.scrollLeft !== exchangeRatesWidth) {
            exchangeRates.scrollTo(exchangeRates.scrollLeft + 1, 0);
        } 

    }, 15);
    
    function isElementInViewport(el) {
        var rect = el.getBoundingClientRect();
        return rect.right > 0;
    }
}

const tickerStyle = {
    backgroundColor: 'gainsboro',
}

export const Exchange = () => {
    const [data, setData] = useState();

    const url = 'https://api.coingecko.com/api/v3/exchange_rates';

    useEffect(() => {
        axios.get(url).then((response) => {
            console.log(response.data.rates);
            setData(response.data.rates);
            setTimeout(initScroll, 500);
        }).catch((error) => {
        });
    }, [])

    if (!data) return <div className='text-center'>
        <Time/>
        <div id='loading' className='fs-1 my-5 py-1'>Loading...</div>
    </div>
    
    return (
        <div style={tickerStyle}>
            <p className='text-center'><span className='textShadowWhite dropShadow px-1'>xCHANGE</span> rates (BTC1) updated <Time/>courtesy of <a href='https://www.coingecko.com/' target='_blank' rel='noreferrer'>CoinGecko</a>.</p>
            <div id='exchangeRates'>
                <Rates data={data} />
            </div>
        </div>
    );
}