import { useContext, useEffect, useState } from 'react'
import { Navbar, Nav } from 'react-bootstrap';
import { OverlayTrigger, Popover, } from 'react-bootstrap';
import { Routes, Route, NavLink, useLocation } from "react-router-dom";
import { Header } from './Header';
import { Home } from './Home';
import { CreateAccount } from './Account';
import { Deposit } from './Deposit';
import { Withdraw } from './Withdraw';
import { UserData } from './UserData';
import { Footer } from './Footer';
import { CurrentUser, UserContext } from '../context/context';
import '../styles/navbar.css';

const navTogglerStyle = {
    WebkitFilter: 'drop-shadow(1px 1px 1px white)',
    filter: 'drop-shadow(1px 1px 1px white)',
    border: '2px solid black !important',
    borderRadius: '2px !important',
    fontVariant: 'small-caps',
    letterSpacing: '-1px',
}

const spanStyle = {
    color: '#990033',
}

const popoverStyle = {
    opacity: 0.9,
}

export const MyNavbar = () => {
    const [expanded, setExpanded] = useState(false);
    const ctx = useContext(UserContext);
    const currentUser = useContext(CurrentUser);
    const [user, setUser] = useState(currentUser);

    const ScrollToTop = () => {
        const { pathname } = useLocation();
        useEffect(() => {
            window.scrollTo(0, 0);
        }, [pathname]);
        return null;
    }

    useEffect(() => {
        sessionStorage.setItem('xChangeCurrentUser', JSON.stringify(user));
      }, [user]);

    return (
        <div>

            <Navbar id='navbar'
                expanded={expanded}
                expand='md'
                className="navbar navbar-light sticky-top">
                <Navbar.Toggle 
                    className="navbar-toggler fw-bolder text-black"
                    type="button"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarTogglerDemo02"
                    aria-controls="navbarTogglerDemo02"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                    onClick={() => setExpanded(expanded ? false : 'expanded')}
                    style={navTogglerStyle}
                >M<span style={spanStyle}>e</span>nu
                </Navbar.Toggle>
                <Navbar.Collapse className="collapse navbar-collapse" id="navbarTogglerDemo02">
                    <Nav className="navbar-nav me-auto mb-2 mb-lg-0" defaultActiveKey={1}>

                        <OverlayTrigger
                            placement="bottom"
                            trigger={['hover', 'focus']}
                            overlay={(
                            <Popover style={popoverStyle}>
                                <h6 className='px-2'><span style={{fontVariant:'small-caps'}}>The Future of Currency</span><hr/><span className='fw-bolder'>MIT | xCHANGE</span><hr/>We have all the current currency <span className='fw-bolder'>xCHANGE</span> rates! </h6>
                            </Popover>
                        )}>
                            <Nav.Link eventKey={1} 
                                id='homeNavLink' 
                                onClick={() => setExpanded(false)} 
                                href='/' 
                                as={NavLink} 
                                to='/'
                            ><span>H<span className='redLetter'>o</span>me</span>
                            </Nav.Link>
                        </OverlayTrigger>

                        <OverlayTrigger
                            placement="bottom"
                            trigger={['hover', 'focus']}
                            overlay={(
                            <Popover style={popoverStyle}>
                                <h6 className='px-2'>Create Account<hr/>Log In<hr/>Entry Validation<hr/>Unique User Emails</h6>
                            </Popover>
                        )}>
                            <Nav.Link eventKey={2} 
                            id='accountNavLink' 
                            onClick={() => setExpanded(false)} 
                            href='/account/' 
                            as={NavLink} 
                            to='/account/'
                            ><span>A<span className='redLetter'>c</span>count</span>
                            </Nav.Link>
                        </OverlayTrigger>

                        <OverlayTrigger
                            placement="bottom"
                            trigger={['hover', 'focus']}
                            overlay={(
                            <Popover style={popoverStyle}>
                                <h6 className='px-2'>Deposit Funds<hr/>Transaction Notes</h6>
                            </Popover>
                        )}>
                             <Nav.Link eventKey={3} 
                                id='depositNavLink' 
                                onClick={() => setExpanded(false)} 
                                href='/deposit/' 
                                as={NavLink} 
                                to='/deposit/'
                            ><span>D<span className='redLetter'>e</span>posit</span>
                            </Nav.Link>
                        </OverlayTrigger>

                        <OverlayTrigger
                            placement="bottom"
                            trigger={['hover', 'focus']}
                            overlay={(
                            <Popover style={popoverStyle}>
                                <h6 className='px-2'>Withdraw Funds<hr/>Transaction Notes<hr/>Overdraft Protection</h6>
                            </Popover>
                        )}>
                            <Nav.Link eventKey={4} 
                                id='withdrawNavLink' 
                                onClick={() => setExpanded(false)} 
                                href='/withdraw/' 
                                as={NavLink} 
                                to='/withdraw/'
                            ><span>W<span className='redLetter'>i</span>thdraw</span>
                            </Nav.Link>
                        </OverlayTrigger>

                        <OverlayTrigger
                            placement="bottom"
                            trigger={['hover', 'focus']}
                            overlay={(
                            <Popover style={popoverStyle}>
                                <h6 className='px-2'>Log In Credentials<hr/>Transaction History</h6>
                            </Popover>
                        )}>
                             <Nav.Link eventKey={5} 
                                id='userDataNavLink' 
                                onClick={() => setExpanded(false)} 
                                href='/userdata/' 
                                as={NavLink} 
                                to='/userdata/'
                            ><span>U<span className='redLetter'>s</span>er Data</span>
                            </Nav.Link>
                        </OverlayTrigger>
                       
                    </Nav>
                </Navbar.Collapse>
            </Navbar>

            <Header 
                user={user}
                setUser={setUser}
            />

            <ScrollToTop />

            <Routes>
                <Route path="/*" element={<Home />} />
                <Route path="/account" 
                        element={<CreateAccount 
                        ctx={ctx}
                        user={user}
                        setUser={setUser}
                    />} 
                />
                <Route path="/deposit" 
                        element={<Deposit
                        ctx={ctx}
                        user={user}
                    />} 
                />
                <Route path="/withdraw" 
                        element={<Withdraw
                        ctx={ctx}
                        user={user}
                    />} 
                />
                <Route path="/userdata" 
                        element={<UserData
                        ctx={ctx}
                        setUser={setUser}
                    />} 
                 />
            </Routes>

            <Footer />

        </div>
    );
}